<template>
  <div class="verticalScreen"
       ref="verticalScreen"
       :style="transform"
       style="width: 433.952px; height: 818px;">
    <div class="play"
         id="frameGameDiv">
      <iframe style="width:100%;height:100%"
              id="frameGame"
              frameborder="0"
              marginwidth="0"
              marginheight="0"
              src="https://www.baidu.com"></iframe>
    </div>
    <div class="btn"
         @click="transformIframe">
      <img class="icon"
           src="./xuanzhuan.png" />
      <div class="text">旋转屏幕</div>
    </div>

  </div>
</template>

<script>
let counter = 0
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data () {
    return {
      transform: {
        transform: ''

      }
    }
  },
  methods: {
    transformIframe () {
      if (counter === 0) {
        this.transform.transform = 'rotate(-90deg)'
        console.log(this.transform.transform)
        counter = 1
      } else {
        this.transform.transform = 'rotate(0deg)'
        counter = 0
      }
    }
  }
}
</script>

